.dark-theme {
    --accent-bg-color: #00a6ff;
    --accent-hover-color: #0088d1;
    --accent-text-color: #ffffff;
    --accent-subtext-color: #707070;
    --primary-bg-color: #1E1E1E;
    --secondary-bg-color: #121212;
    --text-color: #DDDDDD;
    --subtext-color: #aaaaaa;
    --border-color: #474747;
    --button-color: #696969;
    --button-hover-color: #474747;
    --shadow: 1px 1px 5px 1px rgba(0,0,0,0.30);
    --bottom-shadow: 0 4px 4px -2px rgba(0,0,0,0.23);
    --delete-color: #af3636;
    --delete-text-color: #DDDDDD;
    --delete-hover-color: #7c1515;

    --link-color: #F3ABAA;
    --transparent-background: rgba(33,33,33,0.5);

    /* padding */
    --l-padding: 20px;
    --m-padding: 10px;
    --s-padding: 5px;
    --xs-padding: 2px;

    /* border */
    --border-radius: 5px;

    /* typography */
    --default-font-size: 1em;
}

.light-theme {
    --accent-bg-color: #0088CF;
    --accent-hover-color: #00669C;
    --accent-text-color: #ffffff;
    --accent-subtext-color: #b9b9b9;
    --primary-bg-color: #FFFFFF;
    --secondary-bg-color: #e2e2e2;
    --text-color: #161616;
    --subtext-color: #313131;
    --border-color: #d8d8d8;
    --button-color: #696969;
    --button-hover-color: #474747;
    --shadow: 1px 1px 5px 1px rgba(0,0,0,0.30);
    --bottom-shadow: 0 4px 4px -2px rgba(0,0,0,0.23);
    --delete-color: #d13c3c;
    --delete-text-color: #DDDDDD;
    --delete-hover-color: #a02c2c;

    --link-color: #F3ABAA;
    --transparent-background: rgba(33,33,33,0.5);

    /* padding */
    --l-padding: 20px;
    --m-padding: 10px;
    --s-padding: 5px;
    --xs-padding: 2px;

    /* border */
    --border-radius: 5px;

    /* typography */
    --default-font-size: 1em;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video, input, button, select, text {
	margin: 0;
	padding: 0;
	font-size: 100%;
	font: inherit;
    font-weight: 500;
	vertical-align: baseline;
    box-sizing: border-box;
}

body, input, select, textarea, button {
    color: var(--text-color);
    font-weight: 500;
    font-size: var(--default-font-size);
    font-family: 'Quicksand', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

body, #root, .App {
